<template>
  <div id="app">
    <el-button :disabled="disabled" type="success">提交</el-button>
    <el-button loading type="primary">取消</el-button>
    <el-button :isloading="isLoading" icon="el-icon-edit" type="primary" @click.native="clicked"></el-button>
  </div>
</template>
<script>
import ELutton from "./components/el-element"
export default {
  name: 'App',
  data(){
    return {
      disabled:false,
      isLoading:''
    }
  },
  components: {
    'el-button':ELutton
  },
  mounted(){//生命周期函数
    setTimeout(()=>{
      this.disabled=true
    },2000)
  },
  methods:{
    clicked:function(){
      this.isLoading='el-icon-loading'
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



