<template>
    <button class="el-button" :disabled="buttonDisabled"  
    :class="[type?'el-button--'+type:'',{'is-disabled':buttonDisabled,'is-loading':loading}]">
        <i :class="isloading" ></i>
        <i :class="icon" v-if="icon&&!loading"></i>
        <slot></slot>
    </button>
</template>
<script>
export default {
    name:'el-button',
    props:{
        disabled:Boolean,
        type:{
            type:String,
            default:'default'
        },
        loading:Boolean,
        isloading:String,
        icon:{
            type:String,
            defalut:''
        }
    },
    computed:{
        buttonDisabled(){
            return this.disabled;
        }
    }
}
</script>
<style >

</style>



